<template>
  <div>
    <div class="alert-backdrop" v-if="duplicate"></div>
    <div class="alert-container" v-if="duplicate">
      <div class="alert-content">
        <p>이미 조회한 번호입니다.</p>
        <button @click="goBack">돌아가기</button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  methods: {
    goBack() {
      this.$router.push('/');
    },
  },
  data() {
    return {
      duplicate: false
    };
  },
  mounted() {
    const storedPhone = localStorage.getItem('phones');
    const queryPhone = this.$route.query.phone;

    if (queryPhone) {
      let phones = storedPhone ? JSON.parse(storedPhone) : [];

      if (!phones.includes(queryPhone)) {
        phones.push(queryPhone);
        localStorage.setItem('phones', JSON.stringify(phones));
      } else {
        this.duplicate = true;
      }
    }
  }
};
</script>

<style scoped>
.alert-backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.9);
  z-index: 10;
}

.alert-container {
  position: fixed;
  left: 50%;
  top: 40%;
  transform: translate(-50%, -50%);
  min-width: 240px;
  max-width: 300px;
  z-index: 11;
}

.alert-content {
  text-align: center;
  padding: 50px 0px;
  min-width: 280px;
  border: 1px solid #ccc;
  border-radius: 8px;
  background-color: white;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
}

.alert-content p {
  font-size: 18px;
  margin-bottom: 20px;
}

.alert-content button {
  background-color: red;
  color: white;
  padding: 8px 16px;
  border: none;
  border-radius: 4px;
  font-size: 16px;
  cursor: pointer;
}

.alert-content button:hover {
  background-color: #ccc;
}
</style>
