<template>
  <div class="home-container">
    <div class="logo-container">
      <img src="https://msf.or.kr/sites/all/themes/msfkorea/images/logo.svg" alt="로고 이미지">
    </div>
    <div class="phone-input">
      <h2>국경없는의사회 한국<br/> </h2>
      <h2 style="padding:0px 0px 20px 0px;">정기후원자 인증</h2>
      <label class="phone-label" for="phone">후원자님의 휴대폰 번호</label>
      <div class="input-button-container">
        <input
          type="tel"
          id="phone"
          v-model="inputPhoneNumber"
          @input="handleInput"
          placeholder="숫자만 입력해주세요"
        >
        <button @click="showConfirmationDialog = true">조회</button>
      </div>
      <div style="line-height: 20px; margin-top:20px; font-size:12px; width:100%; position:fixed; bottom:30px; left:50%; transform:translateX(-50%)">
        <p> ● 입력하신 번호는 후원자 확인 외 용도로 사용되지 않으며,</p>
        <p>조회 내역은 행사 종료 후 즉시 파기 예정입니다.</p>
      </div>
      <div v-if="showConfirmationDialog" class="confirmation-dialog">
        <p v-if="!showErrorMessage">입력하신 번호가 맞나요?</p>
        <p v-if="!showErrorMessage" style="height:30px">( {{  formatPhoneNumber(inputPhoneNumber) }} )</p>
        <div style="height: 30px;" v-if="showErrorMessage" class="error-message">
          <p>번호를 다시 확인해주세요</p>       
        </div>
        <div class="confirmation-buttons">
          <button @click="cancelFind">취소</button>
          <button @click="findPhone">확인</button>
        </div>
      </div>
    </div>
  </div>
</template>


<script>
import data from "@/assets/data.json";

export default {
  data() {
    return {
      inputPhoneNumber: "",
      list: data,
      showConfirmationDialog: false,
      showErrorMessage: false
    };
  },
  methods: {
    handleInput() {
      this.inputPhoneNumber = this.inputPhoneNumber.replace(/[^0-9]/g, "");
      this.showErrorMessage = false; // Clear error message when input changes
    },
    findPhone() {
      const phoneNumberToFind = this.inputPhoneNumber;
      
      if (phoneNumberToFind.length !== 11) {
        this.showErrorMessage = true;
        return; // Exit early if phone number length is not 11
      }

      const foundItem = this.list.find(item => item.phone === phoneNumberToFind);
      if (foundItem) {
        this.$router.push(`/check?phone=${encodeURIComponent(foundItem.phone)}`);
      } else {
        this.$router.push(`/fail`);
      }
    },
    cancelFind() {
      this.inputPhoneNumber = "";
      this.showConfirmationDialog = false;
      this.showErrorMessage = false; // Clear error message when cancelling
    },
    formatPhoneNumber(phoneNumber) {
      const cleaned = ('' + phoneNumber).replace(/\D/g, '');
      const match = cleaned.match(/^(\d{3})(\d{4})(\d{4})$/);
      if (match) {
        return match[1] + ' ' + match[2] + ' ' + match[3];
      }
      return phoneNumber;
    }
  },
};
</script>

<style scoped>
.home-container {
  position:relative;
  height: 100vh;
  text-align: center;
  padding: 20px;
}

.logo-container {
  text-align: center;
  margin: 40px 0;
}

.logo-container img {
  max-width: 180px;
  height: auto;
}

.phone-input {
  margin-top: 60px;
}

.phone-label {
  display: inline-block;
  width: 300px;
  height: 30px;
  padding-bottom: 10px;
  text-align: center;
  font-size: 16px;
  font-weight: bold;
  font-family: 'Arial', sans-serif; /* 세련된 폰트를 사용하도록 변경 */
}

.input-button-container {
  display: flex;
  align-items: center;
  justify-content: center;
}

input[type="tel"] {
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 16px;
  outline: none;
}

button {
  margin-left: 8px;
  padding: 8px 16px;
  background-color: red;
  color: white;
  border: none;
  border-radius: 4px;
  font-size: 16px;
  cursor: pointer;
}

button:hover {
  background-color: #ccc;
}

.confirmation-dialog {
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: white;
  border: 1px solid #ccc;
  border-radius: 8px;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
  padding: 20px;
  min-width: 240px;
  max-width: 300px;
  text-align: center;
  z-index: 1;
}

.confirmation-content {
  margin-bottom: 16px;
}

.confirmation-buttons button {
  background-color: red;
  color: white;
  margin: 0 8px;
  padding: 8px 16px;
  border: none;
  border-radius: 4px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.confirmation-buttons button:hover {
  background-color: #ccc;
}



</style>
