<template>
  <canvas ref="colorfulFlowerfallCanvas" class="colorful-flowerfall-canvas"></canvas>
</template>

<script>
export default {
  mounted() {
    this.setupCanvas();
    this.startColorfulFlowerfall();
  },
  methods: {
    setupCanvas() {
      this.canvas = this.$refs.colorfulFlowerfallCanvas;
      this.ctx = this.canvas.getContext('2d');
      this.canvas.width = window.innerWidth;
      this.canvas.height = window.innerHeight;
    },
    startColorfulFlowerfall() {
      const flowers = [];
      const shapeTypes = ['circle', 'triangle', 'square', 'diamond', 'ellipse'];
      const animationDuration = 5000; // 5 seconds in milliseconds
      let startTime = Date.now();
      
      const getRandomColor = () => {
        const letters = '0123456789ABCDEF';
        let color = '#';
        for (let i = 0; i < 6; i++) {
          color += letters[Math.floor(Math.random() * 16)];
        }
        return color;
      };
      
      const createFlower = () => {
        const shapeType = shapeTypes[Math.floor(Math.random() * shapeTypes.length)];
        const flower = {
          x: Math.random() * this.canvas.width,
          y: Math.random() * -10,
          size: Math.random() * 10 + 5,
          speed: Math.random() * 4 + 1,
          color: getRandomColor(),
          type: shapeType,
        };
        flowers.push(flower);
      };

      const updateFlowers = () => {
        const currentTime = Date.now();
        const elapsedTime = currentTime - startTime;
        if (elapsedTime < animationDuration) {
          for (let i = flowers.length - 1; i >= 0; i--) {
            const flower = flowers[i];
            flower.y += flower.speed;
            
            if (flower.y >= this.canvas.height) {
              flowers.splice(i, 1);
            }
          }
        } else {
          flowers.length = 0; // Clear the flowers array to stop the animation
        }
      };

      const drawFlowers = () => {
        this.ctx.clearRect(0, 0, this.canvas.width, this.canvas.height);
        flowers.forEach((flower) => {
          this.ctx.beginPath();
          if (flower.type === 'circle') {
            this.ctx.arc(flower.x, flower.y, flower.size, 0, Math.PI * 2);
          } else if (flower.type === 'triangle') {
            this.ctx.moveTo(flower.x, flower.y - flower.size);
            this.ctx.lineTo(flower.x - flower.size, flower.y + flower.size);
            this.ctx.lineTo(flower.x + flower.size, flower.y + flower.size);
          } else if (flower.type === 'square') {
            this.ctx.rect(flower.x - flower.size, flower.y - flower.size, flower.size * 2, flower.size * 2);
          } else if (flower.type === 'diamond') {
            this.ctx.moveTo(flower.x, flower.y - flower.size);
            this.ctx.lineTo(flower.x - flower.size, flower.y);
            this.ctx.lineTo(flower.x, flower.y + flower.size);
            this.ctx.lineTo(flower.x + flower.size, flower.y);
          } else if (flower.type === 'ellipse') {
            this.ctx.ellipse(flower.x, flower.y, flower.size * 2, flower.size, 0, 0, Math.PI * 2);
          }
          this.ctx.closePath();
          this.ctx.fillStyle = flower.color;
          this.ctx.fill();
        });
      };

      setInterval(() => {
        createFlower();
      }, 100);

      const loop = () => {
        updateFlowers();
        drawFlowers();
        requestAnimationFrame(loop);
      };

      loop();
    },
  },
};
</script>

<style scoped>
.colorful-flowerfall-canvas {
  position: fixed;
  top: 0;
  left: 0;
  pointer-events: none;
}
</style>
