<template>
  <div class="result-container">
    <div class="message">
      <h3 v-if="this.$route.query.phone">후원자님 안녕하세요</h3>
      <p style="margin-top:20px; font-size:15px; letter-spacing:-1px;">사진전에 와주셔서 감사합니다.<br/> 직원에게 화면을 보여주신 후 <br/> 커피쿠폰을 수령하세요.</p>
    </div>
    <Alert v-if="duplicate" />
    <FireworksCanvas />
  </div>
</template>

<script>
import Alert from "@/components/Alert.vue";
import FireworksCanvas from "@/components/FireworksCanvas.vue";

export default {
  components: {
    Alert,
    FireworksCanvas
  },
  data() {
    return {
      duplicate: false
    };
  },
  mounted() {
    const storedPhone = localStorage.getItem('phones');
    const queryPhone = this.$route.query.phone;

    if (queryPhone) {
      let phones = storedPhone ? JSON.parse(storedPhone) : [];

      if (!phones.includes(queryPhone)) {
        phones.push(queryPhone);
        localStorage.setItem('phones', JSON.stringify(phones));
      } else {
        this.duplicate = true;
      }
    }
  },
};
</script>

<style scoped>
.result-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 200px;
  margin-top: 100px;
}

.message {
  text-align: center;
}

.message h3 {
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 10px;
}

.message p {
  font-size: 18px;
  margin-bottom: 10px;
}
</style>
